import React from "react";
import { NotFoundPage, HomePage, TestPage } from "pages";
import { useRoutes } from "react-router-dom";

const AppRouter: React.FC = () => {
  const homePage = {
    path: "/",
    element: <HomePage />,
  };

  const testPage = {
    path: "/test/:testNumber",
    element: <TestPage />,
  };

  const notFound = {
    path: "*",
    element: <NotFoundPage />,
  };

  const routing = useRoutes([testPage, homePage, notFound]);

  return <>{routing}</>;
};

export { AppRouter };
