import { QuestionControl } from "components/questionControl";
import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import { useMatch, useNavigate } from "react-router-dom";
import { database } from "repositories/database";

const TestPage: React.FC = () => {
  const navigate = useNavigate();
  const match = useMatch("/test/:testNumber");
  const testNumber = Number(match?.params.testNumber);

  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [showResult, setShowResult] = useState(false);
  const [success, setSuccess] = useState(false);
  const [score, setScore] = useState(0);

  const questions = database.tests.getTest(testNumber);

  const nextText = currentQuestion < questions.length - 1 ? "next" : "finish";

  const answered = (success: boolean) => {
    setSuccess(success);
    setShowResult(true);
    if (success === true) setScore(score + 1);
  };

  const next = () => {
    if (currentQuestion < questions.length - 1) {
      setShowResult(false);
      setCurrentQuestion(currentQuestion + 1);
    } else {
      database.progress.record({
        testId: testNumber.toString(),
        takenOn: new Date(),
        correctAnsweredQuestions: score,
      });
      navigate("/");
    }
  };

  return (
    <article className="m-2">
      <h5>
        {currentQuestion + 1} from {questions.length}
      </h5>
      <QuestionControl
        question={questions[currentQuestion]}
        answered={answered}
      />
      {showResult ? (
        success ? (
          <div className="m-4">
            <Button variant="success" onClick={next}>
              Correct, {nextText}
            </Button>
            <p>{questions[currentQuestion].description}</p>
          </div>
        ) : (
          <div className="m-4">
            <p>{questions[currentQuestion].description}</p>
            <Button variant="warning" onClick={next}>
              Incorrect, {nextText}
            </Button>
          </div>
        )
      ) : null}
    </article>
  );
};

export { TestPage };
