import React from "react";
import ThemeProvider from "react-bootstrap/ThemeProvider";
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Brand from "react-bootstrap/NavbarBrand";
import Toggle from "react-bootstrap/NavbarToggle";
import Collapse from "react-bootstrap/NavbarCollapse";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";
import { database } from "repositories/database";

const AppLayout: React.FC = ({ children }) => {
  const tests = [];
  for (
    let testNumber = 1;
    testNumber <= database.tests.getTestsCount();
    testNumber++
  ) {
    tests.push(
      <NavDropdown.Item key={testNumber} href={`test/${testNumber}`}>
        Test {testNumber}
      </NavDropdown.Item>
    );
  }

  return (
    <ThemeProvider>
      <Navbar bg="dark" expand="sm" variant="dark">
        <Container>
          <Brand href=".">
            <img
              src="logo192.png"
              width="30"
              height="30"
              className="d-inline-block align-top"
              alt="logo"
            />{" "}
            Life in the UK
          </Brand>
          <Toggle aria-controls="basic-navbar-nav" />
          <Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href=".">Progress</Nav.Link>
              <NavDropdown title="Tests" id="basic-nav-dropdown">
                {tests}
              </NavDropdown>
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
      <Container>{children}</Container>
    </ThemeProvider>
  );
};

export { AppLayout };
