import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Question } from "types";

const QuestionControl: React.FC<{
  question: Question;
  answered: (success: boolean) => void;
}> = ({ question, answered }) => {
  const useCheckbox = question.answers.length > 1;

  const defaultState: Record<string, boolean> = {};

  const [state, setState] = useState(defaultState);
  const [hideCheckFor, setHideCheckFor] = useState([] as string[]);

  const check = () => {
    setHideCheckFor([...hideCheckFor, question.id]);

    const isCorrect =
      question.options
        .map(
          (option) =>
            (question.answers.indexOf(option) >= 0 && state[option] === true) ||
            (question.answers.indexOf(option) < 0 && state[option] !== true)
        )
        .indexOf(false) < 0;

    answered(isCorrect);
  };

  const select = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (useCheckbox) {
      setState({
        ...state,
        [event.target.id]: event.target.checked,
      });
    } else {
      setState({
        [event.target.id]: event.target.checked,
      });
    }
  };

  return (
    <article>
      <Form>
        <h3>{question.text}</h3>
        {question.options.map((option, index) => (
          <Form.Group key={index} className="mb-3" controlId={option}>
            <Form.Check
              type={useCheckbox ? "checkbox" : "radio"}
              label={option}
              onChange={select}
              checked={state[option] || false}
            />
          </Form.Group>
        ))}

        {hideCheckFor.indexOf(question.id) < 0 ? (
          <Button onClick={check}>Check</Button>
        ) : null}
      </Form>
    </article>
  );
};

export { QuestionControl };
